header {
  box-sizing: border-box;
  background-color: lightslategray;
  box-shadow: 0px 3px 6px;
  padding: 15px;
}

header input {
  display: inline-block;
  color: black;
  font-size: 34px;
  font-weight: 500;
  text-transform: lowercase;
  align-self: center;
  border: none;
}

header::before {
  content: 'r/';
  display: inline-block;
  color: black;
  font-size: 34px;
  font-weight: 500;
  text-transform: lowercase;
  background: none;
  border: none;
  outline: none;
  appearance: none;
}


.title {
  box-sizing: border-box;
  background-color: lightcyan;
  box-shadow: 0px 3px 6px;
  padding: 15px;
}